.tab-menu-container{height:49px}.tab-menu{display:-ms-flexbox;display:flex;position:fixed;bottom:0;width:100%;height:48px;background-color:#fff;border-top:1px solid #e4e2e2}.tab-menu .btn{-ms-flex:1 1;flex:1 1;-ms-flex-direction:column;flex-direction:column;text-decoration:none;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center}.tab-menu .btn .text{color:#666;font-size:10px;margin-top:4px}.tab-menu .btn.active .text{color:#ff4c64}.tab-menu .btn.active .icon.movie{background-image:url(../assets/icons/movie-solid.png)}.tab-menu .btn.active .icon.user{background-image:url(../assets/icons/user-solid.png)}.tab-menu .btn .icon{display:block;width:22px;height:22px;background-color:#ccc}.tab-menu .btn .icon.movie{background:#fff url(../assets/icons/movie.png) 50%/contain no-repeat}.tab-menu .btn .icon.user{background:#fff url(../assets/icons/user.png) 50%/contain no-repeat}


