.image-slider{position:fixed;top:0;bottom:0;left:0;right:0;background-color:#000;z-index:100}.image-slider .index{color:#fff;text-align:center;height:40px;line-height:40px}.image-slider .image-slider-wrap{position:absolute;top:50%;left:0;-webkit-transform:translateY(-50%);-ms-transform:translateY(-50%);transform:translateY(-50%);width:100%}.image-slider .item{outline:none;height:100%;position:relative}.image-slider .item>img{width:100%;height:100%}


