.t-tag{display:inline-block;padding:2px 4px;font-size:12px;line-height:14px;height:14px;border-radius:4px}.t-tag:nth-child(n+2){margin-left:6px}.t-tag-red{color:#ef6a79;border:1px solid #ef6a79}.t-tag-blue{color:#58a4e8;border:1px solid #58a4e8}.t-btn{width:51px;height:29px;line-height:27px;border-radius:14px;text-align:center;color:#fff;font-size:12px;background-color:#ef6a79;border:none}.t-operator{display:-ms-flexbox;display:flex;-ms-flex-pack:justify;justify-content:space-between;height:40px;line-height:40px}.t-operator .icon{width:40px;height:40px}.t-operator .icon.icon-setting{background:transparent url(../icons/setting.svg) 0/20px 20px no-repeat}.t-operator .icon.icon-message{background:transparent url(../icons/message.svg) 100%/20px 20px no-repeat}.t-operator .icon.icon-back{background:transparent url(../icons/back.svg) 0/20px 20px no-repeat;background-position-x:-5px;background-size:18px}.t-operator .icon.icon-back-black{background:transparent url(../icons/back-black.svg) 0/20px 20px no-repeat;background-position-x:-5px;background-size:18px}.t-operator .icon.icon-share-black{background:transparent url(../icons/share-black.svg) 100%/20px 20px no-repeat}.t-operator .icon.icon-share{background:transparent url(../icons/share.svg) 100%/20px 20px no-repeat}


