.city-layer{position:fixed;top:0;bottom:0;left:0;right:0;overflow:hidden;background-color:#fff}.city-layer .title{top:0;width:100%;line-height:44px;font-size:14px;font-weight:700;border-bottom:1px solid #e2e2e2}.city-layer .title,.city-layer .title .close{position:absolute;height:44px;text-align:center}.city-layer .title .close{width:44px;font-weight:400}.city-layer .content{position:absolute;width:100%;top:44px;bottom:0;background-color:#f5f5f5;overflow-y:scroll;overflow-x:hidden;-webkit-overflow-scrolling:touch}.city-layer .content .city-block .label{font-size:12px;line-height:22px;color:#9f9fa0;padding-left:15px}.city-layer .content .city-block .wrap{display:-ms-flexbox;display:flex;-ms-flex-wrap:wrap;flex-wrap:wrap;list-style:none;padding:10px 15px;margin-left:-12px}.city-layer .content .city-block .item{width:116px;height:32px;line-height:32px;background-color:#fff;color:#999;margin-bottom:10px;margin-left:12px;text-align:center}.city-layer .content .city-list .item,.city-layer .content .city-list .label{color:#333;font-size:12px}.city-layer .content .city-list .label{height:28px;line-height:28px;padding:0 15px;border-top:1px solid #e4e2e2;border-bottom:1px solid #e4e2e2}.city-layer .content .city-list .wrap{margin:0;padding:0 15px;background-color:#fff}.city-layer .content .city-list .item{list-style:none;height:48px;line-height:48px}.city-layer .content .city-list .item:nth-child(n+2){border-top:1px solid #e4e2e2}.city-layer .index{position:absolute;right:0;padding:0 6px;top:80px}.city-layer .index .list{width:40px;padding:0;list-style:none}.city-layer .index .item{list-style:none;text-align:center;margin-bottom:4px;font-size:12px}.city-layer .index .item a{color:#5fb5f9;text-decoration:none}


