.seat-selected{border-top:1px solid #e4e2e2;position:fixed;bottom:0;width:100%;background-color:#fff}.seat-selected .list{display:-ms-flexbox;display:flex;list-style:none;overflow-x:auto;white-space:nowrap;margin:0 10px;padding:0}.seat-selected .list .item{margin-top:10px;margin-bottom:10px}.seat-selected .list .item:nth-child(n+2){margin-left:10px}.seat-selected .movie-ticket{display:-ms-flexbox;display:flex;width:84px;height:36px;border:1px solid #e4e2e2;border-radius:4px;padding:2px 0}.seat-selected .movie-ticket .seat-detail{padding-left:10px;-ms-flex:1 1;flex:1 1}.seat-selected .movie-ticket .pos{font-size:14px;line-height:20px;color:#000}.seat-selected .movie-ticket .price{font-size:12px;line-height:16px;color:#82c57b}.seat-selected .movie-ticket .icon-close{width:20px;background:transparent url(../../../assets/icons/close.svg) 50%/10px no-repeat}.seat-selected .buy-btn{width:100%;height:56px;color:#fff;background-color:#ee7981;text-align:center;line-height:56px}.seat-selected .icon-tag{display:inline-block;width:15px;height:14px;line-height:14px;font-size:12px;color:#fff;background-color:#82c57b;text-align:center;font-style:normal}


